<template>
	<div>
		<div class="cart_head">
			<i style="background: transparent"></i>
			<span></span>
			<div>
				<i class="iconfont icon-shouhoukefu" style="background: transparent;font-size: 18px"></i>
				<span style="font-size:14px;"></span>
			</div>
		</div>
		<div class="message_info1">
			<div class="message_info" v-for="(item,index) in list" :key='index' :id="item.id">
				<ul>
					<li>
						<div class="c1">
							<input @click="chooseclick(index,item)"  type="radio"  class="inp1" ind='true' checked="true" :iqew="strc" />
						</div>
							<img :src="item.imgs"/>
						<div class="c3">
							<span>{{item.name}}</span>
							<span class="cc3"><span>{{item.money*item.order_num}}</span><span>{{item.order_num}}</span></span>
						</div>
					</li>
				</ul>
			</div>
		</div>
		<div class="bottom_cart">
			<div>
				<input id="inp21" @click="chooseclick2" type="checkbox"  checked="true"/><span></span>
			</div>
			<div>
				<span><span>{{nummoney}}</span></span>
				<span>(<span>0.00</span>)</span>
			</div>
			<div>
				<span>({{someshul}})</span>
			</div>
		</div>
	</div>
</template>

<script>
	export default{
		name:"cart",
		data(){
			return{
				list:[],
				lisss:[],
				li:[],
				checkin:'',
				tu1:'',
				name:'',
				ShowPrice:'',
				nummoney:0,
				strc:'',
				shul:12322,
				someshul:0
			}
		},
		methods:{
			chooseclick2(){
				// var someshul=0;
				//input
				var dwc= document.getElementById("inp21")
				// 
				for(var q=0;q<this.list.length;q++){
					//inputq
					var dc= document.getElementsByClassName("inp1")[q]
					// qinputq
					if(q==q){
						// inputindfalse
						if(dc.getAttribute("ind")=='false'){
							// indtrue
							dc.setAttribute('ind','true')
						}else{
							// 
							dc.setAttribute('ind','false')
						}
						// str
						var str=dc.getAttribute("ind")
						// console.log(str)
						if(dwc.checked==true){
							dwc.checked==false
							this.nummoney+=this.list[q].money*this.list[q].order_num
							this.someshul+=this.list[q].order_num
							dc.checked=true	
						}else{
							this.nummoney-=this.list[q].money*this.list[q].order_num
							this.someshul-=this.list[q].order_num
							dc.checked=false
						}
					}
				}
				
				console.log(dwc)
			},
			zj(){
				//var
				var mon=0;
				var shul=0;
				var someshul=0;
				//lissslisssmoney
				for(var f=0;f<this.lisss.length;f++){
//					console.log(this.lisss[f].money)
					//mon()money()order_num()
					 mon+=Number(this.lisss[f].money)*Number(this.lisss[f].order_num)
					 someshul+=Number(this.lisss[f].order_num)
					 shul+=Number(this.lisss[f].order_num)
				}
				console.log(shul)
				this.shul = shul
				console.log(mon)
				//nummoney
				this.nummoney=mon
				this.someshul=someshul
			},
			chooseclick(index,item){
				// console.log(item)
				// console.log(this.lisss)
				var dc= document.getElementsByClassName("inp1")[index]
				// console.log(dc.checked)
				this.list.map((a,e)=>{
//					console.log(e)
					if(e==index){
						if(dc.getAttribute("ind")=='false'){
							dc.setAttribute('ind','true')
						}else{
							dc.setAttribute('ind','false')
						}
						var str=dc.getAttribute("ind")
						console.log(str)
						//indtrue
						if(str=="true"){
							//()
							this.nummoney=this.nummoney+item.money*item.order_num;
							this.shul=this.shul+item.order_num
							this.someshul+=item.order_num
							dc.checked=true
							console.log(dc.checked)
						}else{
							//()
							this.nummoney=this.nummoney-item.money*item.order_num;
							this.someshul-=item.order_num
							this.shul=this.shul-Number(item.order_num)
							console.log(this.shul)
							dc.checked=false
//							console.log(this.zj)
						}
					}
				})
			},
			cheackin(){
				//
				this.$axios({
					url:"/users/isLogin",
					method:"get",
					params:{
//						user:this.checkin
					}
				}).then(res=>{
					console.log(res)
					if(res.data.err==''){
						this.$router.push({path:'/login'})
					}else{
						this.$axios({
							url:"/viadante/chakan",
							method:"post",
							data:{
								
							}
						}).then(res=>{
							console.log(res)
							this.lisss=res.data
							console.log(this.lisss)
							this.zj()
							for(var i=0;i<this.lisss.length;i++){
								this.list=this.lisss
								this.name=this.lisss.name
							}
						})
					}	
				})
			},
		},
		mounted(){
			this.cheackin()
			this.lisss=this.$store.state.carno
		}
	}
</script>

<style lang="less" >
	.cart_head{
		background: white;
		position: fixed;
		top: 0;
		/*z-index: 999;*/
		width: 100%;
		height: 80px;
		display: flex;
		justify-content: space-between;
		align-items: center;
		span{
			background: white;
			font-size: 35px;
			font-weight: 700;
		}
		i{
			width: 45px;
			height: 42px;
			background: yellowgreen;
			margin-right: 30px;
		}
	}
	.message_info1{
		margin-bottom: 213px;
		margin-top: 80px;
	}
	.message_info{
		
		width: 100%;
		height: 230px;
		/*background: red;*/
		ul{
			width: 100%;
			height: 230px;
			li{
				background: white;
				/*border-bottom: 1px solid black;*/
				width: 100%;
				height: 230px;
				display: flex;
				justify-content: flex-start;
				align-items: center;
				.c1{
					margin: 0 20px 0 20px;
					input{
						background: white;
					}
				}
				img{
					width: 220px;
				}
				.c3{
					height: 100%;
					display: flex;
					/*flex-direction: row;*/
					align-content: space-around;
					justify-content: flex-start;
					flex-wrap: wrap;
					span{
						background: white;
					}
					.cc3{
						/*background: white;*/
						width: 100%;
						display: flex;
						justify-content: space-between;
						span{
							background: white;
						}
					}
				}
			}
		}
	}
	.bottom_cart{
		position: relative;
		position: fixed;
		bottom: 106px;
		width: 100%;
		height: 80px;
		background: white;
		border-bottom: 1px solid saddlebrown;
		display: flex;
		
		div:nth-child(2){
			width: 435px;
			color: deepskyblue;
			display: flex;
			flex-direction: column;
			flex-wrap: wrap;
			align-items: flex-end;
		}
		div:nth-child(1){
			width: 135px;
			display: flex;
			justify-content: center;
			align-items: center;
		}
		div:nth-child(3){
			width: 180px;
			background: rgb(242,192,0);
			color: white;
			display: flex;
			justify-content: center;
			align-items: center;
			span{
				background:rgb(242,192,0);
			}
		}
	}
</style>


